@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff')
    format('woff');
  font-weight: 400;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Pretendard-Regular';
}

ul,
ol {
  list-style: none;
}
a {
  font-family: 'Pretendard-Regular';

  text-decoration: none;
  color: #777;
}

.minMax {
  font-family: 'Pretendard-Regular';
  margin: 0 auto;
  width: 1200px;
}
.flexBox-between-start {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.flexBox-between {
  display: flex;
  justify-content: space-between;
}
.flexBox-center {
  display: flex;
  justify-content: center;
}
.flexBox-start {
  display: flex;
  justify-content: flex-start;
}
.flexBox-alignCenter {
  display: flex;
  align-items: center;
}
.flexBox {
  display: flex;
}
