.dropdown {
  position: absolute;
  width: 101px;
  height: 119px;
  margin-top: 3px;
  margin-left: -30px;
  background: #ffffff;
  border: 1px solid #eeeeee;
  box-shadow: 0px 2px 10px rgba(174, 174, 174, 0.23);
  border-radius: 5px;
  text-align: center;
  font-size: 14px;
  z-index: 999;
  ul {
    padding: 5px 0;

    li {
      margin-top: 13px;
      a {
        color: #222222;
      }
    }
  }
  hr {
    width: 90%;
    margin: 10px auto;
    border: none;
    height: 1px;
    background-color: #eeeeee;
  }
  .logout {
    font-size: 14px;
    color: #ff7b7b;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
}
