.studyContainer {
  position: relative;
  border-radius: 10px;
  border: 1px solid #eee;
  background-color: #ffffff;
  align-items: center;
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 25px;
  position: relative;
  &:hover {
    border: 1px solid #605cff;
  }
  .memberCount {
    margin-top: 15px;

    color: #999;
    span {
      font-weight: 600;
      color: #605cff;
    }
  }
  .clickHeart {
    position: absolute;
    top: 20px;
    right: 20px;
    &:hover {
      transform: scale(110%);
    }
  }
  cursor: pointer;
  .leaderTag {
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: #f7f7ff;
    color: #605cff;
    border-radius: 20px;
    padding: 4px 12px;
    font-size: 13px;
  }

  p.date {
    color: #999;
  }
  h3 {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-top: 7px;
    color: #222;
  }
  ul.skills {
    margin-top: 15px;
    width: 205px;
    overflow: hidden;
    text-overflow: ellipsis;
    // p {
    //   margin-right: 7px;
    //   width: 45px;
    //   img {
    //     display: block;
    //     width: 45px;
    //   }
    // }
  }
  span.ellipsis {
    position: absolute;
    right: 25px;
  }
}
